export const sortFunction = (e, users, sorted, setSorted) => {
  const usersCopy = users;

  //Sort Title Column
  if (e === "sortByTitle") {
    usersCopy.sort((a, b) => {
      if (sorted.reversed) {
        //Descending
        return b.title.localeCompare(a.title);
      }
      //Ascending
      return a.title.localeCompare(b.title);
    });
    setSorted({ reversed: !sorted.reversed });
  }

  //Sort Artist Column
  else if (e === "sortByArtist") {
    usersCopy.sort((a, b) => {
      if (sorted.reversed) {
        return b.artist.localeCompare(a.artist);
      }
      return a.artist.localeCompare(b.artist);
    });
    setSorted({ reversed: !sorted.reversed });
  }
};

