import { Data as data } from "../../playlists/playlists";
import "../styles/playlistComp.css";

export const PlaylistComponent = (setSelectedPlaylist, setSongs, setSearch) => {
  return (
    <div className="relative py-8 sm:py-8 xl:py-3 min-w-screen min-h-screen flex items-center justify-center text-white">
      <div className="grid border-x lg:grid-cols-6 sm:grid-cols-3 grid-cols-2 lg:divide-x divide-solid divide-white">
        {Object.keys(data).map((eachPlaylist, index) => {
          return (
            <article
              key={index}
              className="group h-full flex flex-col divide-y text-sm text-center divide-gray-400 px-2"
            >
              <div className="sm:pb-8 pb-3 num">{index + 1}</div>
              <div className="sm:py-3 py-1.5 uppercase font-light">
                {eachPlaylist}
              </div>
              <div className="sm:h-28 h-20 flex items-center text-xs">
                <p className="break-words font-light whitespace-pre-line mx-auto w-28">
                  Professionalism is dedication multiplied on time
                </p>
              </div>
              <div className="sm:py-3 py-1.5 uppercase">
                Total songs - {data[eachPlaylist].length}
              </div>
              <div className="py-8 text-9xl relative">
                <div
                  id="playlistName"
                  onClick={() => {
                    setSelectedPlaylist(eachPlaylist);
                    setSongs(data[eachPlaylist]);
                    setSearch("");
                  }}
                  className="sm:pt-36 pt-32 sm:pb-0 pb-1 uppercase w-full max-w-full -scale-100 stretch bg-black sm:group-hover:-translate-y-56 group-hover:-translate-y-44 group-hover:transition-transform flex items-center"
                >
                  {eachPlaylist}
                </div>
              </div>
            </article>
          );
        })}
      </div>
    </div>
  );
};
