import { useEffect } from "react";
import NoSleep from "nosleep.js";
import "./chords.css";

export const Chords = () => {
  useEffect(() => {
    const noSleep = new NoSleep();

    var checkbox = document.querySelector("input[id=sleep-checkbox]");
    checkbox.addEventListener("change", function () {
      if (this.checked) {
        console.log("Enabled NoSleep");
        noSleep.enable();
        alert(`Enable noSleep mode`);
      } else {
        console.log("Disabled NoSleep");
        noSleep.disable();
      }
    });
  }, []);

  return (
    <>
      <input type="checkbox" id="sleep-checkbox" on />
      <label for="sleep-checkbox" className="toggle">
        <img
          alt="noSleep"
          src="https://png.pngtree.com/png-vector/20220608/ourmid/pngtree-wobbly-plastic-eyes-eye-toy-png-image_4919384.png"
        />
      </label>
    </>
  );
};
