import { useState } from "react";
import { Link } from "react-router-dom";
import { sortFunction } from "../utils/searchSortPaginationTableFunctions";
import { Data as data } from "../../playlists/playlists";
import { PlaylistComponent } from "./PlaylistComponent";
import "../styles/tableComp.css";

export const TableComponent = () => {
  const [selectedPlaylist, setSelectedPlaylist] = useState("");
  const [songs, setSongs] = useState(data[selectedPlaylist]);
  const [search, setSearch] = useState("");
  const [sorted, setSorted] = useState({ reversed: false });
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage, setPostsPerPage] = useState(100);
  let paginationBtn = [];
  const searchPrompt = "Search song by title/artist...";
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  for (
    let i = 1;
    i <=
    Math.ceil(
      data[selectedPlaylist]?.length /
        (postsPerPage ? postsPerPage : data[selectedPlaylist]?.length)
    );
    i++
  ) {
    paginationBtn.push(i);
  }

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const searchFn = (e) => {
    setSearch(e.target.value.toLowerCase());
    setCurrentPage(1);
  };
  const paginationFn = (e) => {
    setPostsPerPage(e.target.value);
    setCurrentPage(1);
  };

  return (
    <>
      {selectedPlaylist === "" ? (
        <>
          <Link to="/chords" id="chords">
            <img
              alt="chords"
              src="https://images.ctfassets.net/r1mvpfown1y6/ooVmFWSSBwSqCQmYcoIg6/0ea6c988a3e023079a448fb0b7736d7d/16108-what-is-a-chord-Hero.jpg"
            />
          </Link>
          {PlaylistComponent(setSelectedPlaylist, setSongs, setSearch)}
        </>
      ) : (
        <div id="tableRoot">
          <input
            type="search"
            id="searchBar"
            placeholder={
              navigator.platform === "Win32"
                ? "🔍 " + searchPrompt
                : searchPrompt
            }
            onChange={searchFn}
          />
          <button
            id="backBtn"
            onClick={() => {
              setSelectedPlaylist("");
            }}
          >
            BACK
          </button>

          <hr />
          <div id="tablePagination">
            <div id="tablePaginationNavigator">
              <button
                id="paginationNavPrev"
                disabled={currentPage === 1}
                onClick={() => setCurrentPage(currentPage - 1)}
              >
                ⏮ Previous
              </button>
              {paginationBtn.map((number) => (
                <button
                  id={currentPage === number ? "paginationBtns" : null}
                  key={number}
                  onClick={() => paginate(number)}
                >
                  {number}
                </button>
              ))}
              <button
                id="paginationNavNxt"
                disabled={currentPage === Math.ceil(data.length / postsPerPage)}
                onClick={() => setCurrentPage(currentPage + 1)}
              >
                Next ⏭
              </button>
            </div>
            <div id="tablePaginationRowsPerPage">
              <input
                type="number"
                min="1"
                placeholder="Songs per page"
                value={postsPerPage}
                onChange={paginationFn}
              />
            </div>
          </div>
          <hr />
          <table id="playlistTable">
            <thead>
              <tr>
                <th>🔢</th>
                <th
                  onClick={() =>
                    sortFunction("sortByTitle", songs, sorted, setSorted)
                  }
                >
                  Title
                </th>
                <th
                  onClick={() =>
                    sortFunction("sortByArtist", songs, sorted, setSorted)
                  }
                >
                  Artist
                </th>
                <th>Song</th>
              </tr>
            </thead>
            <tbody>
              {songs
                .filter(
                  (user) =>
                    user.title.toLowerCase().includes(search) ||
                    user.artist.toLowerCase().includes(search)
                  // fields.some((label) => user[label].toLowerCase().includes(search))
                )
                .slice(indexOfFirstPost, indexOfLastPost) //Table (startFrom, endFrom )
                .map((eachSong, e) => (
                  <tr key={eachSong.id}>
                    <td>{eachSong.id}</td>
                    <td>{eachSong.title}</td>
                    <td>{eachSong.artist}</td>
                    <td>
                      <audio
                        controls
                        loop
                        // onEnded={(event) => {
                        //   const audio_elements =
                        //     document.getElementsByTagName("audio");
                        //   for (let i = 0; i < audio_elements.length - 1; i++) {
                        //     const audio_element = audio_elements[i + 1];
                        //     audio_element.play();
                        //   }
                        // }}
                        onPlay={(event) => {
                          const audio_elements =
                            document.getElementsByTagName("audio");
                          for (let i = 0; i < audio_elements.length; i++) {
                            const audio_element = audio_elements[i];
                            if (audio_element !== event.target) {
                              audio_element.pause();
                            }
                          }
                          navigator.mediaSession.metadata = new MediaMetadata(
                            songs.filter((e) => e.title === eachSong.title)?.[0]
                          );
                        }}
                      >
                        <source
                          src={`music/${selectedPlaylist}/${eachSong.title}.mp3`}
                          type="audio/mpeg"
                        />
                        Your browser does not support the audio element.
                      </audio>
                    </td>
                  </tr>
                ))}
            </tbody>
          </table>
        </div>
      )}
    </>
  );
};
